.footer {
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  max-width: 100%;
  min-height: 77px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $footer-text-color;
  background-color: $modal-votes-bg-color;
  flex-direction: column;
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 19px;
    flex-direction: row;
  }
}
.footer__like {
  display: flex;
  align-items: center;
  margin-top: 10px;
  @media screen and (min-width: 768px) {
    margin-top: 0px;
    margin-left: 4px;
  }
}
.icon-favorit {
  margin-left: 10px;
  margin-right: 10px;
  width: 14px;
  height: 13px;
}
.footer__link {
  position: relative;
  &::after {
    content: '';
    width: 100%;
    height: 2px;
    position: absolute;
    bottom: 0px;
    left: 0;
    background-color: $modal-text-color;
  }
  &:hover,
  &:focus {
    cursor: pointer;
    color: #000;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}
